import Sidebar from "./components/sidebar/Sidebar";
import Topbar from "./components/topbar/Topbar";
import "./app.css";
import Home from "./components/pages/home/Home";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import UserList from "./components/pages/userList/UserList";
import User from "./components/pages/user/User";
import NewUser from "./components/pages/newUser/NewUser";
import ProductList from "./components/pages/productList/ProductList.jsx";
import Product from "./components/pages/product/Product.jsx";
import NewProduct from "./components/pages/newProduct/NewProduct";

function App() {
  return (
    <BrowserRouter>
      <Topbar />
      <div className="container">
        <Sidebar />
        <div className="content">
          <Routes>
            <Route path="/admin-dashboard-2/" element={<Home />} />
            <Route path="/" element={<Home />} />
            <Route path="admin-dashboard-2/users/*" element={<UserList />} />
            <Route path="admin-dashboard-2/user/:UserId" element={<User />} />
            <Route path="admin-dashboard-2/newUser" element={<NewUser />} />

            <Route path="admin-dashboard-2/products" element={<ProductList />} />
            <Route path="admin-dashboard-2/product/:productId" element={<Product />} />
            <Route path="admin-dashboard-2/newproduct" element={<NewProduct />} />

          </Routes>
        </div>
      </div>
    </BrowserRouter>
  );
}

export default App;
